import { Component, Input, Output, EventEmitter } from '@angular/core';
import '@avl-controls/core/dist/avl-textfield/avl-textfield';
import '@avl-controls/core/dist/avl-button/avl-button';

@Component({
  selector: 'avl-login',
  templateUrl: './avl-login.component.html',
  styleUrls: ['./avl-login.component.css']
})
export class AvlLoginComponent {
  /**
   *
   * ### Styling
   * The following custom properties are available for styling:
   *
   * Custom property                          | Description                      | Default
   * :----------------------------------------|:---------------------------------|:-------------------------
   * `--avl-login-background-color`           | Background color of avl login    | `#2F2F2F`
   * `--avl-tab-bar-background-color`         | Background color of tab bar      | `transparent`
   *
   */
  _styling: any;

  @Input() username: string | undefined = undefined;
  @Output() usernameChange = new EventEmitter<string>();
  @Input() password: string | undefined = undefined;
  @Output() passwordChange = new EventEmitter<string>();
  @Input() errorMessage: string | undefined = undefined;
  @Output() login = new EventEmitter<{ username: string, password: string }>();
  @Input() currentYear = '2021';
  @Input() productLogo: string;
  @Input() productName: string;
  @Input() brandPromise: string;

  private _open = false;
  @Input()
  set open(value: boolean) {
    this._open = value;
    this.openChange.emit(this._open);
  }
  get open(): boolean {
    return this._open;
  }
  @Output() openChange = new EventEmitter<boolean>();

  constructor() {}

  loginHandler() {
    this.login.emit({
      username: this.username,
      password: this.password
    });
  }

  handleUsernameChange(username) {
    this.usernameChange.emit(username);
  }

  handlePasswordChange(password) {
    this.passwordChange.emit(password);
  }

  closeDialog() {
    this.open = false;
  }

  /** @hidden */
  openChanged(value: boolean) {
    this.open = value;
  }
}
