import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';

import '@avl-services/vanilla-js-services/avl-localization-service/avl-localization-service.api';
import '@avl-controls/core/dist/avl-button/avl-button';
import { AvlAboutDialogInfo, VersionInfo } from './avl-about-dialog.model';

@Component({
  selector: 'avl-about-dialog',
  templateUrl: './avl-about-dialog.component.html',
  styleUrls: ['./avl-about-dialog.component.css']
})
export class AvlAboutDialogComponent implements OnInit {
  @Input() avlAboutDialogInfo: AvlAboutDialogInfo;

  private _open = false;
  @Input()
  set open(value: boolean) {
    this._open = value;
    this.openChange.emit(this._open);
  }
  get open(): boolean {
    return this._open;
  }
  @Output() openChange = new EventEmitter<boolean>();

  /**
   *
   * ### Styling
   * The following custom properties are available for styling:
   *
   * Custom property                                      | Description                             | Default
   * :----------------------------------------------------|:----------------------------------------|:----------------------
   * `--avl-about-dialog-foreground-color`                | Main foreground color                   | `--avl-corporate-foreground-color`
   * `--avl-about-dialog-foreground-medium-color`         | Forground color for version information | `--avl-corporate-foreground-medium-color`
   * `--avl-about-dialog-link-color`                      | Link color                              | `--avl-corporate-link-color`
   * `--avl-about-dialog-link-hover-color`                | Link hover color                        | `--avl-corporate-link-hover-color`
   * `--avl-about-dialog-on-button-background-color`      | Button text color                       | `--avl-corporate-on-button-background-color`
   * `--avl-about-dialog-button-background-color`         | Button background color                 | `--avl-corporate-button-background-color`
   * `--avl-about-dialog-button-background-hover-color`   | Button hover color                      | `--avl-corporate-button-background-hover-color`
   * `--avl-about-dialog-button-background-focus-color`   | Button focus color                      | `--avl-corporate-button-background-focus-color`
   * `--avl-about-dialog-button-background-pressed-color` | Button pressed color                    | `--avl-corporate-button-background-pressed-color` *
   */
  _styling: any;

  constructor() {}

  public ngOnInit(): void {
    this.translateLabels();
  }

  private async translateLabels() {
    if (!AvlLocalizationService || !AvlLocalizationService.translationService) {
      return;
    }

    const translationService = AvlLocalizationService.translationService;

    this.avlAboutDialogInfo.productName = await this.validateAndTranslate(
      this.avlAboutDialogInfo.productName
    );
    this.avlAboutDialogInfo.brandPromise = await this.validateAndTranslate(
      this.avlAboutDialogInfo.brandPromise
    );
    this.avlAboutDialogInfo.versionInfo = await this.translateVersionInfo(
      this.avlAboutDialogInfo.versionInfo
    );
    this.avlAboutDialogInfo.legalText = await this.validateAndTranslate(
      this.avlAboutDialogInfo.legalText
    );
    this.avlAboutDialogInfo.additionalInfoLabel = await this.validateAndTranslate(
      this.avlAboutDialogInfo.additionalInfoLabel
    );
    this.avlAboutDialogInfo.okButtonLabel = this.avlAboutDialogInfo.okButtonLabel
      ? await translationService.translate(this.avlAboutDialogInfo.okButtonLabel)
      : 'OK';
  }

  private async validateAndTranslate(key: string) {
    const translateService = AvlLocalizationService.translationService;
    return key ? await translateService.translate(key) : key;
  }

  private async translateVersionInfo(versionInfo: VersionInfo[]): Promise<VersionInfo[]> {
    return versionInfo
      ? await Promise.all(versionInfo.map(async item => await this.translateVersionInfoItem(item)))
      : versionInfo;
  }

  private async translateVersionInfoItem(item: VersionInfo): Promise<VersionInfo> {
    const translateService = AvlLocalizationService.translationService;
    return {
      ...item,
      label: await translateService.translate(item.label)
    };
  }

  closeDialog() {
    this.open = false;
  }

  /** @hidden */
  openChanged(value: boolean) {
    this.open = value;
  }
}
