import { Component, ViewChild, AfterViewInit, HostBinding } from '@angular/core';

import '@avl-controls/core/dist/avl-icon-button/avl-icon-button';
import '@avl-controls/core/dist/avl-tooltip/avl-tooltip';

import type { ElementRef } from '@angular/core';

@Component({
  selector: 'avl-dialog-header',
  templateUrl: './avl-dialog-header.component.html',
  styleUrls: ['./avl-dialog-header.component.css']
})
export class AvlDialogHeaderComponent implements AfterViewInit {
  @HostBinding('class.extended')
  extended = false;

  @ViewChild('extendedSlot')
  private extendedSlot: ElementRef<HTMLSlotElement>;

  ngAfterViewInit() {
    Promise.resolve().then(
      () => (this.extended = !!this.extendedSlot.nativeElement.childElementCount)
    );
  }
  public closeDialog = (event: MouseEvent) => {
    (event.target as HTMLElement)
      .closest('avl-dialog')
      .dispatchEvent(new CustomEvent('headerCloseClicked'));
  };
}
