import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { A11yModule } from '@angular/cdk/a11y';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

// components
import { AvlDialogComponent } from './avl-dialog/avl-dialog.component';
import { AvlLoginComponent } from './avl-login/avl-login.component';
import { AvlNgBindersModule } from '@avl-controls/ng-binders';
import { AvlDialogHeaderComponent } from './avl-dialog/avl-dialog-header/avl-dialog-header.component';
import { AvlDialogFooterComponent } from './avl-dialog/avl-dialog-footer/avl-dialog-footer.component';
import { AvlDialogContentComponent } from './avl-dialog/avl-dialog-content/avl-dialog-content.component';
import { AvlAboutDialogComponent } from './avl-about-dialog/avl-about-dialog.component';

import { AvlNavigationModule } from '@avl-ng-controls/navigation';
import { AvlCommonModule } from '@avl-ng-controls/common';
import { AvlUnsavedChangesDialogComponent } from './avl-unsaved-changes-dialog/avl-unsaved-changes-dialog.component';

const componentsAndDirectives = [
  AvlDialogComponent,
  AvlLoginComponent,
  AvlDialogHeaderComponent,
  AvlDialogFooterComponent,
  AvlDialogContentComponent,
  AvlAboutDialogComponent,
  AvlUnsavedChangesDialogComponent
];

const providers = [];

@NgModule({
  imports: [
    CommonModule,
    AvlNgBindersModule,
    AvlNavigationModule,
    AvlCommonModule,
    A11yModule,
    FormsModule,
    ReactiveFormsModule
  ],
  declarations: componentsAndDirectives,
  providers: providers,
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  exports: componentsAndDirectives
})
export class AvlDialogModule {}
