import { Component, Input, OnInit } from '@angular/core';
import { UnsavedChangesService, UnsavedChangesShellService } from '@avl-services/ng-services';

@Component({
  selector: 'avl-unsaved-changes-dialog',
  templateUrl: './avl-unsaved-changes-dialog.component.html',
  styleUrls: ['./avl-unsaved-changes-dialog.component.css']
})
export class AvlUnsavedChangesDialogComponent implements OnInit {
  private unsavedChanges: UnsavedChangesService | UnsavedChangesShellService;

  @Input() unsavedChangesService: UnsavedChangesService | UnsavedChangesShellService;

  ngOnInit(): void {
    this.unsavedChanges = this.unsavedChangesService;
  }

  /**
   * Event handler for closing the unsaved changes dialog
   */
  public onOpenChange(open: boolean): void {
    if (!open) {
      this.unsavedChanges.closeDialog();
    }
  }
}
